body {
  margin: 0;
}

h1 {
  margin: 5px 0;
}

h3 {
  margin: 0;
}

nav {
  width: 300px;
  margin: 5px;
  display: flex;
  justify-content: space-evenly;
}

nav>a {
  cursor: pointer;
  padding: 5px;
  text-decoration: none;
  color: black;
}

nav>a:hover {
  color: rgb(0, 61, 76);
  background-color: rgb(108, 194, 74);
}

.even {
  background-color: rgba(0, 61, 76, 0.3);
}

.odd {
  background-color: rgba(108, 194, 74, 0.3);
}

.hero-list {
  list-style-type: none;
  padding: 5px;
  border-bottom: rgb(108, 194, 74) dotted 1px;
}

li>a {
  color: white;
}

.hidden {
  display: none;
}

.warning {
  display: block;
  font-size: 10px;
  color: red;
}

.valid {
  cursor: pointer;
  padding: 8px;
  background-color: rgb(108, 194, 74);
  border: 2px solid rgb(108, 194, 74);
  align-self: center;
  border-radius: 5px;
}

.invalid {
  cursor: not-allowed;
  padding: 8px;
  background-color: rgb(108, 194, 74);
  border: 2px solid rgb(108, 194, 74);
  align-self: center;
  border-radius: 5px;
}

.office-page {
  margin: 5px;
  display: grid;
  grid-template: 100% / 50% 50%;
  justify-content: center;
  align-content: center;
  box-shadow: 5px 5px 20px black;
}

.office-detail {
  color: white;
  grid-row: 1 / span 1;
  grid-column: 1 / span 1;
  background-color: rgb(0, 61, 76);
  padding-left: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  text-align: center;
}

.office-detail>a>img {
  width: 60%;
  margin: 10px;
}

.office-schedule {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  grid-row: 1 / span 1;
  grid-column: 2 / span 1;
  background-color: white;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  text-align: center;
}

.office-schedule>ul {
  text-align: left;
}

.main {
  display: grid;
  grid-template: 100% / auto auto;
  grid-gap: 10px;
}

.main-pics {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-column: 2 / span 1;

}

.main-pics>img {
  width: 100%;
  margin-bottom: 125px;
}

.main-list {
  background-color: rgb(0, 61, 76);
  grid-row: 1 / span 2;
  grid-column: 1 / span 1;
}

.main-list>ul {
  padding-left: 10px;
}

.staff {
  margin: 5px;
}

.main-staff {
  text-align: center;
}

.staff-list {
  background-color: rgba(128, 128, 128, 0.3);
  border-bottom: rgba(0, 61, 76, 0.3) dotted 1px;
}